.alert {
  background-color: #29d2e4;
  border: 1px solid #29d2e4;
  color: #fff;
  padding: 15px 20px;
}

.alert .alert-icon {
  float: left;
  margin-right: 15px;
}

.alert .alert-info {
  margin: 0 10px 0 0;
  text-transform: uppercase;
  font-weight: normal;
  font-size: 14px;
}

.alert .alert-container {
  position: relative;
  max-width: 960px;
  margin: 0 auto;
}

.alert .close-icon {
  float: right;
  color: #000;
  margin-top: 0;
  margin-left: 0;
  width: 21px;
  height: 21px;
  position: relative;
  background: none;
  border: none;
  outline: none;
  cursor: pointer;
  text-indent: -999px;
  overflow: hidden;
  white-space: nowrap;
}

.alert .close-icon:before, .alert .close-icon:after {
  content: '';
  position: absolute;
  top: 8px;
  width: 15px;
  height: 2px;
  left: 0;
}

.alert .close-icon:before {
  background-color: #fff;
  transform: rotate(-45deg);
}

.alert .close-icon:after {
  background-color: #fff;
  transform: rotate(45deg);
}

